

* {
  font-family: monospace; 
  color:white;
}

html,body,*{
  margin:0;
  padding:0;
}



html {
  width: 100vw;
  height:100vh;
}


body, #root{
  width:100%;
  height:100%;
}

#root {
  height:100%;
}



.App {
  text-align: center;
  width:100%;
  height:100%;
  overflow-x:hidden;
}

.background
{
  position:fixed;
  top:0;
  left:0;
  overflow:hidden;
  width:100%;
  height:100%;
  z-index:-9999;
}


.qualif_box_sub_item
{
  padding-left:10px;
  text-align:left
}

.qualif_box_item
{
  text-align:left
}
.home
{
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    padding:40px;
    align-items:center;  

}




.home-text
{
  font-size:50px;
}

.home-text > * 
{
  text-align:left;
}



@media only screen and (max-width: 600px) {


  .home 
  {
    align-items:flex-start;
    padding-top:200px;
  }

  .home-text > * 
{
  text-align:center;
  width:100%;
  font-size:25px;
}



}

.card
{
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    padding:40px;
    align-items:center;  
    box-sizing: border-box;
}

.card:nth-child( odd )
{
    justify-content:flex-end;
}


.card_box
{

  width:600px;
  background-color:rgba(255,255,255,0.3);
  border-radius:3px;
  padding:10px;
  min-width:100px;

}

.card_title
{
  font-size:30px;
}

.card_box_content
{
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  align-items: flex-start;
  word-wrap: break-word;   

}

ul {
    margin-left:15px;
}

li {
    flex-wrap:wrap;
    text-align: left;
    overflow-wrap: anywhere;
}

.card_box_content > *
{
  font-size:20px;
}


.large 
{
    font-size:50px;
}

.small
{
    font-size:15px;
}

.extra_small 
{
    font-size:10px;
}



@media only screen and (max-width: 600px) {


  .card
  {
    height: auto;
    margin-bottom:20px;
    justify-content: center;
  }
  
  .card_box
  {
    width:90%;
  }

  li, a {
    font-size: 11px;
  }

  .card_title
 {
    font-size: 20px;
 }


}
